*{
    margin: 0;
    box-sizing: border-box;
    padding: 0;
}

.coin__important{
    padding: 20px;
    width: 100%;
    background-color: #4062FF;
    border-radius: 5px;
    display: flex;
    flex-direction: row;
    align-items: center;
    justify-content: space-between;
    gap: 30px;
    color: #F9FAFB;
}

.coin__important__leftBlock{
    display: flex;
    flex-direction: row;
    gap: 1em;
}
.coin__important__leftBlock>img{
}

.coin__important__names{
    display: flex;
    justify-content: center;
    flex-direction: column;
}


.coin__important__prices__change{
    display: flex;
    justify-content: center;
    align-items: center;
    background-color: #40BAFF;
    border-radius: 5px;
    padding: 5px;
    font-size: 15px;
}

.coin{
    min-height: 100vh;
    padding: 20px;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    display: flex;
    background-color: #F9FAFB;
}
.coin__important__rank{

}
.navigationHistory{
    display: flex;
}

.links{
}

.cards{
    gap: 5px;
    display: flex;
    justify-content: center;
    align-items: center;
    height: 200px;
    width: 100%;
    margin-top: 20px;
}

.coin__important__price{
    font-size: 32px;
}

.card{
    box-shadow: 0 0 10px #afafaf;
    height: 100%;
    width: 100%;
    border-radius: 2px;
    background-color: white;
    padding: 20px;
    display: flex;
    justify-content: space-between;
    flex-direction: column;
    align-items: unset;
}
.priceChart{
    gap: 10px;
    align-items: center;
    flex-direction: column;
    justify-content: center;
    display: flex;
    padding: 10px;
    margin-top: 20px;
    background-color: white;
    box-shadow: 0 0 10px #b0b0b0;
    width: 100%;
}

.card__header{

}

.phonePriceChart {
    display: none;
}
.card__content{
    font-weight: 700;
    font-size: 22px;
}

@media screen and (max-width: 600px) {
    .coin__important{
        flex-direction: column;
        width: 100vw;
    }
    .cards{
        flex-direction: column;
    }
    .coin{
        padding: 0;
        width: 100vw;
    }
    .priceChart{
        display: none;
    }
    .phonePriceChart{
        width: 100%;
        align-items: center;
        justify-content: center;
        margin-top: 70px;
        display: flex;
        background-color: white;
        box-shadow: 0 0 10px #b0b0b0;
        padding: 10px;
        flex-direction: column;
    }

    .coin__important__prices__change{
        margin-bottom: 20px;
    }
    .coin__important__rank{
        margin-bottom: 20px;
    }
}